import React from 'react';
import {Button,Form,InputGroup} from 'react-bootstrap';
import { Formik } from 'formik';

function FeeForm(props){
    const {schema,handleSubmit,options} = props;
    return(
        <>
        <Formik
            validationSchema={schema}
            initialValues={(props.initValue)? props.initValue : {
              rentalSelection: '',
              Name: '',
              Amount: '',
            }}
            onSubmit={handleSubmit}
            validateOnChange={false}
            validateOnBlur={false}
        >
        {({ 
         errors,handleChange,handleSubmit,touched,initialValues
        }) => (
            <Form onSubmit={handleSubmit}>
              <Form.Row>
                  <Form.Label className="col-sm-4 col-form-label">Select Rental or group</Form.Label>
                  <div className="col-md-4">
                    <Form.Control
                      as="select"
                      name="rentalSelection"
                      onChange={handleChange}
                      isInvalid={!!errors.rentalSelection}
                    >
                    {options.map((opt)=>(<option key={opt} selected={initialValues.rentalSelection===opt}>{opt}</option>))}
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">{errors.rentalSelection}</Form.Control.Feedback>
                  </div>
              </Form.Row>
              <br/>
              <Form.Row>
                  <Form.Label className="col-sm-4 col-form-label">Name of Fee</Form.Label>
                  <div className="col-md-4">
                  <Form.Control
                    type="text"
                    name="Name"
                    onChange={handleChange}
                    isInvalid={!!errors.Name }
                    defaultValue={initialValues.Name}
                  />
                  <Form.Control.Feedback type="invalid">{errors.Name}</Form.Control.Feedback>
                  </div>
              </Form.Row>
              <br/>
              <Form.Row>
                  <Form.Label className="col-sm-4 col-form-label">Type of Fee</Form.Label>
                  <div className="col-md-8">
                  <Form.Check
                    inline
                    name="Type"
                    label="percentage"
                    id={"percentage"}
                    onChange={handleChange}
                    value="percentage"
                    type="radio"
                    defaultChecked={initialValues.Type==="percentage"}
                  />
                  <Form.Check
                    inline
                    name="Type"
                    label="flat USD"
                    id={"flat USD"}
                    onChange={handleChange}
                    value="flatUSD"
                    type="radio"
                    defaultChecked={initialValues.Type==="flatUSD"}
                  />
                  <div style={{fontSize:'0.8em',color:'#dc3545',padding:'0em'}}>{errors.Type}</div>
                  </div>
              </Form.Row>
              <br/>
              <Form.Row>
                  <Form.Label className="col-sm-4 col-form-label">Amount</Form.Label>
                  <div className="col-md-4">
                  <InputGroup className="mb-3">
                    <Form.Control
                      type="number"
                      name="Amount"
                      onChange={handleChange}
                      isInvalid={!!errors.Amount}
                      defaultValue={initialValues.Amount}
                    />
                    <InputGroup.Append>
                      <InputGroup.Text id="basic-addon2">$</InputGroup.Text>
                    </InputGroup.Append>
                  <Form.Control.Feedback type="invalid">{errors.Amount}</Form.Control.Feedback>
                  </InputGroup>
                  </div>
              </Form.Row>
              <br/>
              <Form.Row>
                  <Form.Label className="col-sm-4 col-form-label">Modality</Form.Label>
                  <div className="col-md-8">
                 
                  <Form.Check
                    inline
                    name="Modality"
                    label="per stay"
                    id={"perStay"}
                    onChange={handleChange}
                    value="perstay"
                    type="radio"
                    defaultChecked={initialValues.Modality==="perstay"}
                  />
                  <Form.Check
                    inline
                    name="Modality"
                    label="per night"
                    id={"perNight"}
                    onChange={handleChange}
                    value="pernight"
                    type="radio"
                    defaultChecked={initialValues.Modality==="pernight"}
                  />
                   <Form.Check
                    inline
                    name="Modality"
                    label="per person"
                    id={"perPerson"}
                    onChange={handleChange}
                    value="perperson"
                    type="radio"
                    defaultChecked={initialValues.Modality==="perperson"}
                  />
                  <Form.Check
                    inline
                    name="Modality"
                    label="per person per night"
                    id={"pppn"}
                    onChange={handleChange}
                    value="pppn"
                    type="radio"
                    defaultChecked={initialValues.Modality==="pppn"}
                  />
                  <div style={{fontSize:'0.8em',color:'#dc3545',padding:'0em'}}>{errors.Modality}</div>
                  </div>
              </Form.Row>
              
              <br/>
              <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
                <Button variant="primary" disabled={false} type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>Save changes</Button>
              </div>
          </Form>
        )}
        </Formik>
        </>
    )
}

export default FeeForm;