import React from 'react';
import styles from '../../rates.module.css';

import {Table,Row,Col,Container,Form} from 'react-bootstrap';
import {connect} from 'react-redux';

import BulkRateChange from '../BulkRatechange/BulkRateChange';
import SlideSelect from '../../../../../../components/Slide_select/Slide_select';
import {editRates} from '../../../../../../general_redux/rates/actions';
import BulkDefaultChange from "../BulkDefaultChanges/BullkDefaultChange";

class RatesList extends React.Component{
    state={
        month:new Date().getMonth(),
        currentWeek:[],
        year:"2021",
        period : [
            'This Month','This week'
        ],
        curretweekData:[],
        currentSelect:0,
        selectedRange: 'This Month',
        bulkChangeModal:false,
        bulkDefaultModal:false
    }
   
    componentDidMount(){
        this.getWeeksInMonth();
    }
    getWeeksInMonth=()=>{
        const {month,year}=this.state;
        let weeks=[],
        firstDate=new Date(year, month, 1),
        lastDate=new Date(year, month+1, 0), 
        numDays= lastDate.getDate();
        const weekP=Math.floor((new Date().getDate())/7);
        let start=1;
        let end=7-firstDate.getDay();
        while(start<=numDays) {
            let tmp = [];
            for (let index = start; index <= end; index++) {
                tmp.push(`${index}/${month + 1}/21`);
            }
            weeks.push(tmp);
            start = end + 1;
            end = end + 7;
            if (end > numDays)
                end = numDays;
        }
        this.setState({
            curretweekData:weeks,
            currentWeek: weeks[weekP]
        });
     }

    getWeekData=(status)=>{
        const {curretweekData,currentSelect}=this.state; 
        if(curretweekData && curretweekData.length!==0){
            const len=curretweekData.length;
            const val=currentSelect;
            if(status==="next"){
                this.setState({
                    currentSelect:(val+1) % len
                })
            }
            else if(status==="prev"){
                if(val-1<0){
                    this.setState({
                        currentSelect:0
                    })
                }else{
                    this.setState({
                        currentSelect:(val-1) % len
                    })
                }
            }
        }
    }
    handleEdit=(data)=>{
        const {editRates} =this.props;
        //editRates(data);
        this.setState({
            bulkChangeModal:false
        })
    }
    closeModal = (data) => {
        this.setState({
            bulkDefaultModal:false
        })
    }
    findRentalName=(rentalId)=>{
        let name="";
        const ar=this.props.rentals.filter((row)=>(row.id===rentalId));
        if(ar.length!==0){
            name=ar[0].name;
        }
        return name;
    }
    render(){
        const {currentWeek,curretweekData,period,selectedRange,currentSelect,bulkChangeModal,bulkDefaultModal}=this.state;
        const {rateList,rentals}= this.props;
        return(
        <>
        {(rateList && rateList.length!==0) ? (
            <>
                <BulkRateChange
                    show={bulkChangeModal}
                    onHide={() => this.setState({
                        bulkChangeModal:false
                    })}
                    onEdit={(data)=>this.handleEdit(data)}
                />
                <BulkDefaultChange
                    show={bulkDefaultModal}
                    onHide={this.closeModal}
                />
                <div className={styles.rateSettingHeader}>
                    Rate Settings
                </div>
                <div className={styles.rateBox}>
                    <button className={styles.allBtn} onClick={()=>this.setState({
                        bulkChangeModal:true
                    })}>
                        Bulk rate changes
                    </button>
                    <button className={styles.allBtn} onClick={()=>this.setState({
                        bulkDefaultModal:true
                    })}>
                        Set bulk default rate
                    </button>
                </div>
                {rateList && rateList.length!==0 && rateList.map((rate)=>(
                    <div key={rate}>
                        <div className={styles.rateSettingHeader}>
                            {this.findRentalName(rate.rentalId)}
                        </div>
                        <Container fluid>
                            <Row xs="auto" className={styles.rowSetup}>
                                <Col lg="auto" xs="auto" >
                                    <span className={styles.labelShow}>Default Rate:</span>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <div>{`${rate.dailyRate} USD`}</div>
                                </Col>
                                <Col sm={{span:"auto",offset:1}}>
                                    <input
                                        type="checkbox"
                                        id="allowdiscount"
                                        value="allowdiscount"
                                        readOnly={true}
                                        checked={rate.discount}
                                    />
                                    <label style={{marginLeft:'0.5em'}}className={styles.labelShow}>
                                        Allow discount</label>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}>Weekly discount:</span>
                                </Col>
                                <Col lg={"auto"} xs="auto">
                                    <div>{`${rate.weekDiscount} %`}</div>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}> Monthly discount:</span>
                                </Col>
                                <Col lg={"auto"} xs="auto">
                                    <div>{`${rate.monthDiscount} %`}</div>
                                </Col>
                            </Row>
                            <Row xs="auto" className={styles.rowSetup}>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}>  Default Min stay (days) :</span>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <div>{rate.minstay}</div>
                                </Col>
                                <Col sm={{span:"auto",offset:1}}>
                                    <input
                                        type="checkbox"
                                        id="fixedRate"
                                        value="fixedRate"
                                        readOnly={true}
                                        checked={rate.fixRate}
                                    />
                                    <label style={{marginLeft:'0.5em'}} className={styles.labelShow}>Allow fixed Rate</label>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}>Weekly Price :</span>
                                </Col>
                                <Col lg={"auto"} xs="auto">
                                    <div>{`${rate.weekPrice} USD`}</div>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}>Monthly Price :</span>
                                </Col>
                                <Col lg={"auto"} xs="auto">
                                    <div>{`${rate.monthPrice} USD`}</div>
                                </Col>
                            </Row>
                            <Row xs="auto" className={styles.rowSetup}>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}>Default Extra guest fees (per guest):</span>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <div>{rate.GuestNum}</div>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <span className={styles.labelShow}>Guest per night above :</span>
                                </Col>
                                <Col lg="auto" xs="auto">
                                    <div>{rate.guestLimit}</div>
                                </Col>
                            </Row>
                        </Container>
                        <br/>
                        <SlideSelect
                            list={period}
                            currentSelect={0}
                            getCurrentSelection={(sl)=>this.setState({
                                selectedRange: sl
                            })}
                        />
                        <br/>
                        <Table responsive bordered hover className={styles.table}>
                            <thead>
                            <tr>
                                {selectedRange==="This Month" && (
                                    <>
                                        <th className={styles.slider}
                                            onClick={() => this.getWeekData('prev')}
                                        >
                                            {`<`}
                                        </th>
                                        {curretweekData && curretweekData.length!==0 && curretweekData[currentSelect].map((day)=>(
                                            <th key={day}>{day}</th>
                                        ))}
                                        <th className={styles.slider}
                                            onClick={() => this.getWeekData('next')}
                                        >
                                            {`>`}
                                        </th>
                                    </>
                                )}
                                {selectedRange==="This week" && (
                                    <>
                                        <th></th>
                                        {currentWeek && currentWeek.length!==0 && currentWeek.map((day)=>(
                                            <th key={day}>{day}</th>
                                        ))}
                                        <th></th>
                                    </>
                                )}
                            </tr>
                            </thead>
                            <tbody>
                            {selectedRange==="This Month" && (
                                <>
                                    <tr>
                                        <td>Daily Rate</td>
                                        {curretweekData && curretweekData.length!==0 && curretweekData[currentSelect].map((day)=>(
                                            <td key={day}>{rate.dailyRate}</td>
                                        ))}
                                        <td></td>
                                    </tr>
                                    <tr>
                                        <td>Minimum Stay</td>
                                        {curretweekData && curretweekData.length!==0 && curretweekData[currentSelect].map((day)=>(
                                            <td key={day}>{rate.minstay}</td>
                                        ))}
                                        <td></td>
                                    </tr>
                                </>
                            )}
                            {selectedRange==="This week" && (
                                <>
                                    <tr>
                                        <td>Daily Rate</td>
                                        {currentWeek && currentWeek.length!==0 && currentWeek.map((day)=>(
                                            <td key={day}>{rate.dailyRate}</td>
                                        ))}
                                        <td></td>
                                    </tr>
                                    <tr>
                                        <td>Minimum Stay</td>
                                        {currentWeek && currentWeek.length!==0 && currentWeek.map((day)=>(
                                            <td key={day}>{rate.minstay}</td>
                                        ))}
                                        <td></td>
                                    </tr>
                                </>
                            )}
                            </tbody>
                        </Table>
                    </div>
                ))}
            </>
        ):(
            <>
                Before setting rates, please create a rental.
            </>
        )}

        </>
        )
    }
    
}


const mapStateToProps = state => ({
    rateList: state.rates.rateSettings ,
    rentals:state.rentals.rentals,
});
  
const mapDispatchToProps = dispatch => ({
    editRates: (data)=>{
        dispatch(editRates(data))
    }
});
export default connect(mapStateToProps,mapDispatchToProps)(RatesList);