import React from 'react';
import {Button,Modal} from 'react-bootstrap';
import styles from "../../modules/view/Components/UserSetting/usersetting.module.css";

function ConfirmMessage(props){
    return(
        <Modal
            {...props}
            size="md"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                   Delete Rental
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className={styles.deleteModalBody}>
                <p>
                    Deleting Rental action cann not be undo ,Are you sure you want to delete this rental?
                </p>
            </Modal.Body>
            <div className={styles.modalFooter}>
                <Button variant="primary" onClick={props.onHide} style={{marginRight:'2em'}}>Cancel</Button>
                <Button variant="danger" onClick={props.onConfirmAct}>Delete</Button>
            </div>
        </Modal>
    )
}
export default ConfirmMessage;