import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styles from './login.module.css';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import logo from '../../icons/logo.png';
import {useSelector,useDispatch} from 'react-redux';
import { loginUser } from '../../general_redux/user/actions';

function Login() {
  const [email, setEmail] = useState('');
  const [user, setUser] = useState(null);
  const loginStatus = useSelector(({ user }) => user && user.loginAction);
  const dispatch = useDispatch();
  const login = (e) => {
    e.preventDefault();
    setUser(email);
  }

  useEffect(() => {
    if (user) {
      dispatch(loginUser(user))
    }
  }, [dispatch, user])

  return (
      <div>
        <Header redirect/>
        <div className="d-flex justify-content-center">
          <div className={`${styles.mainContainer} ${styles.login}`}>
            <div className={` ${styles.login_wrapper} shadow`}>
              <div className="d-flex justify-content-center">
                <Link to="/">
                  <img src={logo} className={styles.login_image} />
                </Link>
              </div>
              <div
                  className={`d-flex justify-content-center ${styles.login_header}`}
              >
                <h5>User Login</h5>
              </div>
              <div className="d-flex justify-content-center">
                <form onSubmit={login}>
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                    <span
                        className={`input-group-text ${styles.input_group_text}`}
                        id="emailAddress"
                    >
                      Email Address
                    </span>
                    </div>
                    <input
                        onChange={(e) => setEmail(e.target.value)}
                        type="email"
                        className={`form-control ${styles.form_control}`}
                        aria-label="emailAddress"
                        aria-describedby="emailAddress"
                        required
                    ></input>
                  </div>
                  <div className={styles.center}>
                    <button className={styles.login_submit} type="submit">Login</button>
                  </div>
                </form>
              </div>
              <div className={styles.login_separator}>
                ------------------------- OR -------------------------
              </div>
              <div className="justify-content-center">
                <div className={styles.login_socialmedia}>
                  <form>
                    <button
                        className={` ${styles.login_submit} ${styles.login_google}`}
                    >
                      <i className={`fa fa-google ${styles.fa_google}`}></i> |
                      Sign in with Google
                    </button>
                  </form>
                </div>
                
                <div className={styles.login_socialmedia}>
                  <form>
                    <button
                        className={` ${styles.login_submit} ${styles.login_facebook}`}
                    >
                      <i
                          className={`fa fa-facebook-f ${styles.fa_facebook}`}
                      ></i>{' '}
                      | Sign in with Facebook
                    </button>
                  </form>
                  <Link to="/register" className={styles.login_create}>
                    Create an Account
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
  );
}

export default Login;