import React, {useEffect, useState} from 'react';
import {useSelector} from 'react-redux';
import {Button,Form,InputGroup} from 'react-bootstrap';
import { Formik } from 'formik';

function DefaultChangeForm(props){
    const {schema,handleSubmit,options} = props;
    // const rentals=useSelector(({rates})=>rates && rates.rateSettings);
    // const [values,setValues]=useState();
    const [rental,setRental]=useState('');

    const changeRental=(e,field)=>{
        console.log(field);
        if(field==="rentalOrGroup"){
            setRental(e.target.value);
        }
    }
    return(
        <>
            <Formik
                validationSchema={schema}
                initialValues={(props.initValue)? props.initValue : {
                    guestFee: '',
                    dailyRate:55,
                    discount: false,
                    fixRate: false,
                    guestLimit: null,
                    minStay: 2,
                    monthDiscount: null,
                    monthPrice: null,
                    rentalOrGroup: null,
                    weekDiscount: null,
                    weekPrice: null,
                }}
                onSubmit={handleSubmit}
                validateOnChange={false}
                validateOnBlur={false}
            >
                {({
                      values, errors,handleChange,handleSubmit,touched,initialValues
                  }) => (
                    <Form onSubmit={handleSubmit}>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Select Rental</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    as="select"
                                    name="rentalOrGroup"
                                    onChange={handleChange}
                                    isInvalid={!!errors.rentalOrGroup}
                                >
                                    <option key={'selectR'} selected={true} disabled={true}>----Select----</option>
                                    {options.map((opt)=>(<option key={opt.id} value={opt.id}>{opt.name}</option>))}
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">{errors.rentalOrGroup}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Default rate per night</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="dailyRate"
                                    min={55}
                                    onChange={handleChange}
                                    isInvalid={!!errors.dailyRate }
                                    defaultValue={initialValues.dailyRate}
                                />
                                <Form.Control.Feedback type="invalid">{errors.dailyRate}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Default Min stay</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="minStay"
                                    min={0}
                                    max={15}
                                    onChange={handleChange}
                                    isInvalid={!!errors.minStay }
                                    defaultValue={initialValues.minStay}
                                />
                                <Form.Control.Feedback type="invalid">{errors.minStay}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Default Rate per guest</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="guestFee"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.guestFee }
                                    defaultValue={initialValues.guestFee}
                                />
                                <Form.Control.Feedback type="invalid">{errors.guestFee}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Guest Limit</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="guestLimit"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.guestLimit}
                                    defaultValue={initialValues.guestLimit}
                                />
                                <Form.Control.Feedback type="invalid">{errors.guestLimit}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Group>
                                <Form.Check
                                    type={"checkbox"}
                                    label="Allow discount"
                                    name="discount"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.discount}
                                    defaultValue={initialValues.discount}
                                />
                            </Form.Group>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Weekly discount</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="weekDiscount"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.weekDiscount}
                                    defaultValue={initialValues.weekDiscount}
                                />
                                <Form.Control.Feedback type="invalid">{errors.weekDiscount}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Monthly discount</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="monthDiscount"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.monthDiscount}
                                    defaultValue={initialValues.monthDiscount}
                                />
                                <Form.Control.Feedback type="invalid">{errors.monthDiscount}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Group>
                                <Form.Check
                                    type={"checkbox"}
                                    label="Allow Fixed Rate"
                                    name="fixRate"
                                    onChange={handleChange}
                                    isInvalid={!!errors.fixRate}
                                    defaultValue={initialValues.fixRate}
                                />
                            </Form.Group>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Weekly Price</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="weekPrice"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.weekPrice}
                                    defaultValue={initialValues.weekPrice}
                                />
                                <Form.Control.Feedback type="invalid">{errors.weekPrice}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Monthly Price</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="monthPrice"
                                    min={0}
                                    onChange={handleChange}
                                    isInvalid={!!errors.monthPrice}
                                    defaultValue={initialValues.monthPrice}
                                />
                                <Form.Control.Feedback type="invalid">{errors.monthPrice}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
                            <Button variant="primary" disabled={false} type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>Save changes</Button>
                        </div>
                    </Form>

                )}
                {/*{setValues(values)}*/}
                {/*{setDailyRate(values.dailyRate)}*/}

            </Formik>
        </>
    )
}

export default DefaultChangeForm;