import { toast } from "react-toastify";
import {
  LOGIN_USER,
  USER_TOKEN_VERIFY_SUCCESS,
  LOGOUT_USER,
  REGISTER_USER,
  RESET_WELCOME,
  USER_TOKEN_ADD,
  USER_TOKEN_VERIFY_FAIL
  } from './types';
import {actionForState} from "../../common/functions/utils";
toast.configure();

  const INITIAL_STATE = {
    user: null,
    isNewUser:false,
    authToken: '',
    action: {
      loading: false,
      success: undefined
    },
    loginAction: {
      success: false,
      loading: false,
      error: false
    },
    registerAction: {
      success: false,
      loading: false,
      error: false
    },
  };
  
  export default function (state = INITIAL_STATE, action) {
    switch (action.type) {
      case LOGIN_USER.pending:
        return{
          ...state,
          loginAction: actionForState(state.loginAction, 'pending'),
        }
      case LOGIN_USER.success:
        toast.success("Login Success please check your email!")
        return{
          ...state,
          loginAction: actionForState(state.loginAction, 'success'),
        }
      case LOGIN_USER.error:
        toast.error(action.payload.message);
        return{
          ...state,
          loginAction: actionForState(state.loginAction, 'error',action.payload.message),
        }
      case USER_TOKEN_VERIFY_SUCCESS:
        return {
          ...state,
          user: action.payload.user
        }
        case USER_TOKEN_VERIFY_FAIL:
          return {
            ...state,
            user: null,
            authToken: null
          }
      case USER_TOKEN_ADD:
        return {
          ...state,
          authToken: action.payload
        }
      case REGISTER_USER.pending:
        return {
          ...state,
          registerAction: actionForState(state.loginAction, 'pending'),
        }
      case REGISTER_USER.success:
        toast.success("Account created successfully,Please check your mail!");
        return{
          ...state,
          isNewUser: action.payload.newUser,
          user: action.payload.email,
          registerAction: actionForState(state.loginAction, 'success'),
        }
      case REGISTER_USER.error:
        toast.error(action.payload.message);
        return{
          ...state,
          registerAction: actionForState(state.loginAction, 'error',action.payload.message),
        }
      case LOGOUT_USER:
        return {
          ...state,
          user: null,
          isNewUser:false
        }
      case RESET_WELCOME:
        return {
          ...state,
          isNewUser:false
        }
      default:
        return state;
    }
  }
  