import React from 'react';
import {Form} from 'react-bootstrap';
import { Formik } from 'formik';
import styles from './container.module.css';

function MailingForm(props){
    const {schema,handleSubmit} = props;
    return(
        <>
            <Formik
                validationSchema={schema}
                initialValues={(props.initValue)? props.initValue : {
                    user_name: '',
                    user_email: '',
                }}
                onSubmit={handleSubmit}
                validateOnChange={false}
                validateOnBlur={false}
            >
                {({
                      errors,handleChange,handleSubmit,touched,initialValues
                  }) => (
                    <Form onSubmit={handleSubmit}>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Full Name</Form.Label>
                            <div className="col-md-8">
                                <Form.Control
                                    type="text"
                                    name="user_name"
                                    onChange={handleChange}
                                    isInvalid={!!errors.user_name }
                                    defaultValue={initialValues.user_name}
                                />
                                <Form.Control.Feedback type="invalid">{errors.user_name}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Email</Form.Label>
                            <div className="col-md-8">
                                <Form.Control
                                    type="email"
                                    name="user_email"
                                    onChange={handleChange}
                                    isInvalid={!!errors.user_email }
                                    defaultValue={initialValues.user_email}
                                />
                                <Form.Control.Feedback type="invalid">{errors.user_email}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <div className={`p-2 d-flex justify-content-center`}>
                            <button type="submit" className={styles.joinBtn}>Join!</button>
                        </div>
                    </Form>
                )}
            </Formik>
        </>
    )
}

export default MailingForm;