import moment from 'moment';
import {toast} from "react-toastify";

import { ADD_BOOKING, EDIT_BOOKING } from './types';
import {actionForState} from "../../common/functions/utils";
toast.configure();

const INITIAL_STATE = {
  allBooking: [
    {
      id: 2,
      type: 'booking',
      group: 10,
      start_time: moment().subtract(2, 'day'),
      status: false,
      end_time: moment().add(1, 'day'),
      price: 200,
      tax: 200,
      noOfAdults: 20,
      arrive: "arrive",
      depart: "depart",
      noOfChildren: 11,
      source: "airbnb",
      noOfGuests:10,
      color:"springgreen"
    },
    {
      id: 4,
      type: 'blockdates',
      group: 2,
      start_time: moment().subtract(1, 'day'),
      status: false,
      end_time: moment().add(1, 'day'),
      title:"Blocked"
    }
  ],
  allBlockDates: [],
  action:{
    loading:false,
    success:undefined
  },
  actionForBooking:{
    loading:false,
    success:false,
    error:false
  },
  actionEditBooking:{
    loading:false,
    success:false,
    error:false
  }
};
export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    case ADD_BOOKING.pending:
      return {
        ...state,
        actionForBooking: actionForState(state.action, 'pending')
      };
    case ADD_BOOKING.success:
      return{
        ...state,
        allBooking: state.allBooking.concat(
            {...action.payload.booking,
              id: Date.now(),
              type:"booking",
              group: action.payload.booking.rentalId,
              color:"springgreen",
              start_time:moment(action.payload.booking.arrive,'YYYY-MM-DD'),
              end_time:moment(action.payload.booking.depart,'YYYY-MM-DD'),
        }),
        actionForBooking: actionForState(state.action, 'success')
      }
    case ADD_BOOKING.error:
      toast.error(action.payload.message)
      return {
        ...state,
        actionForBooking: actionForState(state.action, 'error',action.payload.message),
      }
    case EDIT_BOOKING.pending:
      return {
        ...state,
        actionEditBooking:actionForState(state.action,'pending')
      }
    case EDIT_BOOKING.success:
      const BookingArrayData = state.allBooking;
      const updateIndex = BookingArrayData.findIndex(({ id }) => id === action.payload.id);
      if (updateIndex >= 0) {
        BookingArrayData[updateIndex] = action.payload;
      }
      return {
        ...state,
        allBooking: BookingArrayData,
        actionEditBooking:actionForState(state.action,'success')
      };
    case EDIT_BOOKING.error:
      toast.error(action.payload.message)
      return{
        ...state,
        actionEditBooking:actionForState(state.action,'error',action.payload.message)
      }
    default:
      return state;
  }
}
