import React,{useState} from 'react';
import {useSelector,useDispatch} from 'react-redux';
import styles from '../../rates.module.css';
import AddFee from './AddFee/AddFee';
import ListFees from './List/ListFees';
import {addFee,deleteFee,editFees} from '../../../../../../general_redux/rates/actions';

function Fees(props){
    const dispatch=useDispatch();
    const [addFeeModal,setAddFeeModal] =useState(false);
    const [edited,setEdited]= useState(false);

    const cols= [
        {lable: 'Name', key: 'Name'},
        {lable: 'Type', key: 'Type'},
        {lable: 'Amount', key: 'Amount'},
        {lable: 'Modality', key: 'Modality'},
        {lable: 'Actions', key: ''},
    ];
    const rentals=['Rental 1','Rental 2', 'Rental 3'];

    const closeModal = (data) => {
        if(edited){
            dispatch(editFees(data));
        }else{
            dispatch(addFee(data));
        }
        setEdited(null);
        setAddFeeModal(false);
    }
    const deleteRow=(id)=>{
        dispatch(deleteFee(id));
    }

    const editRow=(val)=>{
        setAddFeeModal(true)
        setEdited(val);
    }
    return(
        <>
            <AddFee
                show={addFeeModal}
                onHide={() => {
                    setAddFeeModal(false);
                    setEdited(null); }}
                onEdit={(data) => closeModal(data)}
                viewForEdit={edited}
            />
            <div className={styles.rateSettingHeader}>
                Fees Settings
            </div>
            <button className={styles.allBtn} onClick={()=>setAddFeeModal(true)}>Add Fee</button>
            
            <ListFees
                rentals={rentals}
                cols={cols}
                deleteFun={(id)=>deleteRow(id)}
                editFees={editRow}
            />
        </>
    )
}

export default Fees;