import React, { useState} from 'react';
import {AppTable} from '../../../../../../common/components';
import {useDispatch} from 'react-redux';
import {Container, Row, Col} from 'react-bootstrap';
import { deleteGuest } from '../../../../../../general_redux/guest/actions';

function ListGuests(props) {
    const dispatch = useDispatch();
    const cols = [
        {lable: 'Name', key: 'name'},
        {lable: 'Company', key: 'company'},
        {lable: 'Email', key: 'emailId'},
        {lable: 'Phone', key: 'phoneNo'},
        {lable: 'Actions', key: ''},
    ]
   
    const handleDelete=(row)=>{
        dispatch(deleteGuest(row.id));
    }

    return (
        <div>
            <Container fluid>
                         <AppTable 
                            cols={cols}  
                            tableName="Guests"
                            rows={props.data}
                            isEdit="true"
                            isDelete="true"
                            onDelete={handleDelete}
                            onEdit={props.editGuests}
                        /> 
            </Container>
        </div>
    )
}
export default ListGuests;