import React, { useState} from 'react';
import {AppTable} from '../../../../../../../common/components';
import {useSelector} from 'react-redux';
import styles from '../../../rates.module.css';

function ListFees(props) {
    let feesList = useSelector(({rates}) => rates && rates.feesList);
    
    const {rentals,cols,deleteFun,editFees}=props;

    return (
        <>
            {rentals && rentals.map((rnt,idx)=>(
                <div key={idx} >
                <div className={styles.taxHeader}>
                    {rnt}
                </div>
                <AppTable 
                    cols={cols} 
                    tableName="Fees" 
                    isEdit="true"
                    onEdit={editFees}
                    isDelete="true"
                    rows={feesList.filter((fee)=>fee.rentalSelection===rnt)}
                    onDelete={(row)=>deleteFun(row.id)}
                />
                </div>
            ))}
        </>
    )
}
export default ListFees;