import {
    LOAD_GUEST_LIST,
    ADD_GUEST_IN_LIST,
    DELETE_GUEST,
    EDIT_GUEST_LIST,
} from './types';
import API from "../../config/api-url";
import {BACKEND_API} from "../../config/middleware";

export const addGuests = data => ({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:ADD_GUEST_IN_LIST,
            method: 'post',
            url: API.GUEST_ADD,
            data
        },
    )
});

export const loadGuests = () => ({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:LOAD_GUEST_LIST,
            method: 'get',
            url: API.GUEST,
        },
    )
});

export const deleteGuest=id=>({
    type: DELETE_GUEST,
    payload: id
})

export const editGuest=data=>({
    type: EDIT_GUEST_LIST,
    payload: data
})