import React, { useEffect, useState } from 'react';
import {useSelector} from "react-redux";
import { Row, Col } from 'react-bootstrap';
import styles from './addnewbooking.module.css';
import * as yup from 'yup';
import { Formik, Form, Field } from 'formik';
import moment from 'moment';

function BookingForm(props) {
  const { view, isEdit,guestList,guestDetail,rentals,handleSave } = props;
  const ratesList = useSelector(({rates})=>rates && rates.rateSettings);
  //=============================================
  // SCHEMA REQUIREMENT FOR FORM VALIDATION
  //=============================================

  const SignupSchema = yup.object().shape({
    rental: yup.string().required('Required'),
    adult: yup.number().min(1, 'At least 1 adult is required').required('At least 1 adult is required'),
    children: yup.number().min(0, 'Cannot be a negative number'),
    price: yup.number().min(0, 'Cannot be a negative number'),
    arrival: yup.date().required('Required'),
    departure: yup.date().required('Required'),
  });

  //=============================================
  // STATE FOR BOOKING DETAILS
  //=============================================

  const [values, setValues] = useState();
  const [totalGuest, setTotalGuest] = useState('');
  const [taxes, setTaxes] = useState('');
  const [finalPrice, setFinalPrice] = useState('');
  // const [group, setGroup] = useState('');
  // const [color, setColor] = useState('');
  const [rental, setRental] = useState('');
  const [price,setPrice]=useState('');
  const [populate,setPopulate]=useState();
  const [paymentStatus, setPaymentStatus] = useState('');
  const [checkIn,setCheckIn]=useState('');
  const [checkOut,setCheckOut]=useState('');

  const [guestName,setGuestName]=useState("");
  const [company,setCompany]=useState("");
  const [primaryEmail,setPrimaryEmail]=useState("");
  const [secondaryEmail,setSecondaryEmail]=useState("");
  const [phoneNumber,setPhoneNumber]=useState("");
  const [country,setCountry]=useState("");
  const [streetAddress,setStreetAddress]=useState("");
  const [postalCode,setPostalCode]=useState("");
  const [stateProvince,setStateProvince]=useState("");
  const [addressDetails,setAddressDetail]=useState("")
  const [nationality,setNationality]=useState("");
  const [language,setLanguage]=useState("");
  const [notes,setNotes]=useState("");

  const [defVal,setDefVal]=useState("");

  const TAXRATE = 0.12;
  const paidOption = ['', 'Payment Pending', 'Partial Payment', 'Paid'];
  const sourceOption = ['', 'beehaz', 'Ben Geeks', 'Something Else', 'Other Options'];

  // useEffect(() => {
  //   if (paymentStatus == 'Paid') setColor('springgreen');
  //   if (paymentStatus == 'Partial Payment') setColor('yellow');
  //   if (paymentStatus == 'Payment Pending') setColor('crimson');
  // }, [paymentStatus]);
  useEffect(()=>{
      if(rental!==""){
        const rateData=ratesList.filter((row)=>{
          if(row.rentalId===Number(rental)){
            return row;
          }
        });
        const rentalData=rentals.filter((row)=>(row.id===Number(rental)))

        if(rateData[0] && rentalData[0]){
          setPopulate(rateData[0]);
        }
      }

  },[rental])

  useEffect(()=>{
    if(guestDetail && guestDetail!==""){
      const guestData= guestList.filter((row)=>(row.name===guestDetail.label))
      setDefVal(guestData[0]);
      setGuestName(guestData[0].name);
      setPrimaryEmail(guestData[0].emailId);
    }else {
      setDefVal("");
      setGuestName("");
      setCompany("");
      setPrimaryEmail("");
      setSecondaryEmail("");
      setPhoneNumber("");
      setCountry("");
      setStreetAddress("");
      setPostalCode("");
      setStateProvince("");
      setAddressDetail("");
      setNationality("");
      setLanguage("");
      setNotes("");
    }
  },[guestDetail])
  //=============================================
  // FORM INITIAL VALUE
  //=============================================

  const initVal = () => {
    return {
      adult: 1,
      children: 0,
      price: 0,
    };
  };

  const validate = (field,value) => {
    let errorMessage;
    if(field==="emailId"){
      if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
        errorMessage = 'Invalid email address';
      }
    }else if(field==="name"){
      if(!value || value===""){
        errorMessage = 'Guest name is required';
      }
    }
    return errorMessage;
  };

  const viewEditData = () => {
    let rental = '';
    if (view.group == '0') rental = 'Rental 1';
    if (view.group == '1') rental = 'Rental 2';
    if (view.group == '2') rental = 'Rental 3';
    return {
      rental: rental,
      adult: view.other.adult,
      children: view.other.children,
      arrival: view.other.arrival,
      departure: view.other.departure,
      price: view.other.price,
      paymentStatus: view.other.paymentStatus,
      source: view.other.source,
      checkin: view.other.checkInTime,
      checkout: view.other.checkOutTime,

      guestName: view.name,
      company: view.company,
      primaryEmail: view.email,
      secondaryEmail: view.email2,
      phoneNumber: view.phone,
      country: view.country,
      streetAddress: view.street,
      postalCode: view.zip,
      stateProvince: view.state,
      addressDetails: view.details,
      nationality: view.nationality,
      language: view.lang,
      notes: view.notes,
    };
  };

  //===================================
  // FORM LOOP
  //===================================

  const bookingForm = [
    // { title: 'Select Rental: ', disabled: false, type: 'select', name: 'rental', options: rentals },
    { title: 'Number of Guests: ', disabled: true, type: 'text', name: 'guests', value: totalGuest ,readOnly:false,default:""},
    { title: 'Adult: ', disabled: false, type: 'number', name: 'adult' ,readOnly:false,default:""},
    { title: 'Children: ', disabled: false, type: 'number', name: 'children',readOnly:false,default:"" },
    { title: 'Arrival Date: ', disabled: false, type: 'date', name: 'arrival',readOnly:false ,default:""},
    { title: 'Departure Date: ', disabled: false, type: 'date', name: 'departure',readOnly:false ,default:""},
    { title: 'Price: ', disabled: true, type: 'number', name: 'price',value:price,readOnly:false ,default:""},
    { title: 'Taxes: ', disabled: true, type: 'number', name: 'taxes', value: taxes,readOnly:false ,default:""},
    { title: 'Final Price: ', disabled: true, type: 'number', name: 'finalPrice', value: finalPrice ,default:""},
    { title: 'Payment Status: ', disabled: false, type: 'text', name: 'paymentStatus', default:"Partially paid",readOnly:true},
    { title: 'Source: ', disabled: false, type: 'select', name: 'source', default:"Beehaz",readOnly:true },
    { title: 'Check-in: ', disabled: true, type: 'time', name: 'checkin',readOnly:false,default:"",value:checkIn },
    { title: 'Check-out: ', disabled: true, type: 'time', name: 'checkout' ,readOnly:false,default:"",value:checkOut}
  ];

  const guestForm = [
    { title: 'Name: ', name: 'name' , value: guestName},
    { title: 'Company: ', name: 'company' ,value:company},
    { title: 'Email Address: ', name: 'emailId',value:primaryEmail },
    { title: 'Secondary Email: ', name: 'secondaryEmailId',value:secondaryEmail },
    { title: 'Phone: ', name: 'phoneNo' ,value:phoneNumber},
    { title: 'Country: ', name: 'country',value:country },
    { title: 'Street and nr. : ', name: 'address',value:streetAddress},
    { title: 'Postal Code: ', name: 'postalCode',value:postalCode },
    { title: 'State/Province: ', name: 'stateProvince',value:stateProvince },
    { title: 'AddressDetails: ', name: 'addressDetails',value: addressDetails},
    { title: 'Nationality: ', name: 'nationality',value:nationality },
    { title: 'Language: ', name: 'language',value:language },
    { title: 'Notes: ', name: 'notes',value:notes },
  ];

  //===================================
  // HANDLE SUBMIT
  //===================================

  function handleSubmit() {
    const payload = {
      rentalId: Number(rental),
      price: finalPrice,
      tax: taxes,
      noOfAdults: values.adult,
      arrive: values.arrival,
      depart:values.departure,
      noOfChildren:values.children,
      source: "airbnb",
      noOfGuests:totalGuest,
      }
      //console.log(payload);
      handleSave(payload);
  }

  return (
    <>
      <Formik
        initialValues={isEdit ? viewEditData() : initVal()}
        validationSchema={SignupSchema}
        onSubmit={handleSubmit}
        validateOnChange={false}
        validateOnBlur={false}
      >
        {({ values, errors }) => (
          <div>
            <Form>
              <Row>
                <Col s={12} md={6} className={styles.wrapper}>
                  <h3 className={styles.header}>Booking Details</h3>
                  <Row className={styles.form_wrapper} key={"select rental"}>
                    <div className={styles.form_title}>Select Rentals</div>
                    <Field
                        as="select"
                        name={"rental"}
                        className={(errors['rental'])?styles.form_entry_err:styles.form_entry}
                    >
                      <option value={""} label={"Select Rental"}></option>
                      {rentals.map((opt)=>(
                          <option key={opt.id} value={opt.id}>{opt.name}</option>
                      ))}
                    </Field>
                  </Row>
                  {bookingForm.map((form) => {
                    const err = errors[form.name];
                    const minDate = form.name === 'departure' ? values.arrival : null;
                    const maxDate = form.name === 'arrival' ? values.departure : null;
                    const today = moment().format('YYYY-MM-DD');
                    return(
                        <>
                          {(form.disabled )? (
                              <Row className={styles.form_wrapper} key={form.title}>
                                <div className={styles.form_title}>{form.title}</div>
                                <Field
                                    type={form.type}
                                    name={form.name}
                                    className={err ? styles.form_entry_err : styles.form_entry}
                                    disabled={true}
                                    value={form.value}
                                />
                                <div className={err ? styles.form_error : styles.form_error_hidden}>
                                  {errors[form.name]}
                                </div>
                              </Row>
                          ):(
                              <Row className={styles.form_wrapper} key={form.title}>
                                <div className={styles.form_title}>{form.title}</div>
                                <Field
                                    type={form.type}
                                    name={form.name}
                                    className={err ? styles.form_entry_err : styles.form_entry}
                                    min={form.name == 'arrival' ? today : minDate}
                                    max={maxDate}
                                    defaultValue={form.default}
                                    readOnly={form.readOnly}
                                />
                                <div className={err ? styles.form_error : styles.form_error_hidden}>
                                  {errors[form.name]}
                                </div>
                              </Row>
                          )}
                        </>
                    )
                })}
                </Col>
                <Col s={12} md={6} className={styles.wrapper}>
                  <h3 className={styles.header}>Guest Details</h3>
                  {guestForm.map((form) => {
                    const err = errors[form.name];
                    return (
                      <Row className={styles.form_wrapper} key={form.title}>
                        <div className={styles.form_title}>{form.title}</div>
                        {(guestDetail && guestDetail!=="" )? (
                            <Field
                                readOnly={true}
                                value={defVal[form.name]}
                            />
                        ):(
                            <Field
                                id={form.name}
                                name={form.name}
                                className={err ? styles.form_entry_err : styles.form_entry}
                                value={form.value}
                                validate={(value)=>validate(form.name,value)}
                            />
                        )}
                        <div className={err ? styles.form_error : styles.form_error_hidden}>{errors[form.name]}</div>
                      </Row>
                    );
                  })}
                </Col>
              </Row>
              <Row>
                <Col>
                  <div className={`d-flex justify-content-center ${styles.button_group}`}>
                    <div>
                      <button type="submit" className={styles.settings_save}>
                        {isEdit ? 'Update Changes' : 'Save Changes'}
                      </button>
                    </div>
                  </div>
                </Col>
              </Row>
            </Form>
            {setTotalGuest(Number(values.adult) + Number(values.children))}
            {populate ? setPrice((Number(values.adult)+Number(values.children))* Number(populate.dailyRate)) : setPrice(0)}
            {populate ? setTaxes([(Number(values.adult)+Number(values.children))* Number(populate.dailyRate)]* TAXRATE) : setPrice(0)}
            {populate ? setFinalPrice(
                ((Number(values.adult)+Number(values.children))* Number(populate.dailyRate))+
                ([(Number(values.adult)+Number(values.children))* Number(populate.dailyRate)]* TAXRATE)
                  )
                : setFinalPrice(0)}
            {populate ? setCheckIn(populate.checkInTime):setCheckIn('')}
            {populate ? setCheckOut(populate.checkOutTime):setCheckOut('')}
            {setValues(values)}
            {setRental(values.rental)}
            {setPaymentStatus(values.paymentStatus)}
            {setGuestName(values.guestName)}
            {setCompany(values.company)}
            {setPrimaryEmail(values.primaryEmail)}
            {setSecondaryEmail(values.secondaryEmail)}
            {setPhoneNumber(values.phoneNumber)}
            {setCountry(values.country)}
            {setStreetAddress(values.streetAddress)}
            {setPostalCode(values.postalCode)}
            {setStateProvince(values.stateProvince)}
            {setAddressDetail(values.addressDetails)}
            {setNationality(values.nationality)}
            {setLanguage(values.language)}
            {setNotes(values.notes)}
          </div>
        )}
      </Formik>
    </>
  );
}

export default BookingForm;
