import {toast} from "react-toastify";

import {
    LOAD_GUEST_LIST,
    ADD_GUEST_IN_LIST,
    DELETE_GUEST,
    EDIT_GUEST_LIST,
  } from './types';
  import {actionForState} from "../../common/functions/utils";
toast.configure();

const INITIAL_STATE = {
    guestList: [{company: "test",
    country: "India",
    id:0,
    address: "address",
    emailId: "mail2mail.com",
    name: "adfds",
    notes: "notes",
    phoneNo: "dfsdf",
    postalCode: "postal_code",
    secondaryEmailId: ""}
    ],
    action: {
      loading: false,
      success: undefined
    },
    actionForLoad : {
        loading:false,
        success:false,
        error:false
    },
    actionForEdit : {
        loading:false,
        success:false,
        error:false
    },
  };
  
  export default function (state = INITIAL_STATE, action) {
    switch (action.type) {
      case LOAD_GUEST_LIST.pending:
        return{
            ...state,
            actionForLoad:actionForState(state.action,'pending')
        }
      case LOAD_GUEST_LIST.success:
        return{
            ...state,
            actionForLoad:actionForState(state.action,'success'),
            guestList:action.payload.guests
        }
      case LOAD_GUEST_LIST.error:
        toast.error(action.payload.message)
        return{
            ...state,
            actionForLoad:actionForState(state.action,action.payload.message)
        }
      case ADD_GUEST_IN_LIST.pending:
        return {
          ...state,
          action: actionForState(state.action, 'pending')
        };
      case ADD_GUEST_IN_LIST.success:
        return {
          ...state,
          guestList: state.guestList.concat(action.payload.guest),
          action: actionForState(state.action, 'success')
        };
      case ADD_GUEST_IN_LIST.error:
        toast.error(action.payload.message)
        return {
          ...state,
          action: actionForState(state.action, action.payload.message)
        };
      case DELETE_GUEST:    
        return {    
            ...state,    
            guestList: state.guestList.filter(item => item.id !== action.payload)    
        };
      case EDIT_GUEST_LIST:
      const guestArrayData = state.guestList;
      const updateIndex = guestArrayData.findIndex(({id}) => id === action.payload.id);
      if (updateIndex >= 0) {
        guestArrayData[updateIndex] = action.payload
      } 
      return {    
          ...state,    
          guestList: guestArrayData,
      };
      default:
        return state;
    }
  }
  