import React from 'react';
import { Button, Modal } from 'react-bootstrap';
import styles from '../../../Rentals/rentals.module.css';
import styles2 from './viewBooking.module.css';
import {useSelector} from "react-redux";
import {tConvert} from '../../../../../../common/functions/utils';

function ViewBooking(props) {
  const user = useSelector(({user})=>user && user.user.name);
  const mob = '+1 546436745';
  const email = useSelector(({user})=>user && user.user.emailId);
  const setting=useSelector(({generalSetting})=>(generalSetting && generalSetting.setting));
  const dummyCheckIn="16:13";
  const dummyCheckOut="07:54";

  const { data } = props;

  return (
    <Modal {...props} size="sm" aria-labelledby="contained-modal-title-vcenter" centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">{user}</Modal.Title>
      </Modal.Header>
      <Modal.Body className={styles.deleteModalBody}>
        {data && data.length !== 0 && (
          <div className={styles2.mainContainer}>
            <div className={styles2.sideText}>{mob}</div>
            <div className={styles2.sideText}>{email}</div>
            <br />
            <div className={styles2.mainText}>{`Arrive : ${data.start_time.format('Do MMMM, YYYY')}`}</div>
            <div className={styles2.mainText}>{`Depart : ${data.end_time.format('Do MMMM, YYYY')}`}</div>
            <div className={styles2.sideText}>{`${data.end_time.diff(data.start_time, 'days')} Nights `}</div>
            <div className={styles2.sideText}>{`CheckIn time: ${(setting.timeFormat==="A")?tConvert(dummyCheckIn):dummyCheckIn}`}</div>
            <div className={styles2.sideText}>{`Checkout time:  ${(setting.timeFormat === "A") ? tConvert(dummyCheckOut) : dummyCheckOut}`} </div>
            <div className={styles2.mainText}>{`Total guests : ${data.noOfGuests}`}</div>
            <div className={styles2.sideText}>{`Adult ${data.noOfAdults} children ${data.noOfChildren}`}</div>
            <div className={styles2.mainText}>{`Price : ${data.price} USD`}</div>
            <div className={styles2.sideText}>{`Status : Payment pending`}</div>
            <div className={styles2.mainText}>{`Color : ${data.color}`}</div>
          </div>
        )}
      </Modal.Body>
      <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
        <Button variant="primary" type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>
          Notes
        </Button>
        <Button variant="primary" type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'} onClick={props.onEdit}>
          Edit
        </Button>
      </div>
    </Modal>
  );
}
export default ViewBooking;
