export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    case ADD_DATA:
      return { ...state, tableData: action.payload };
    case UPDATE_DATA:
      return { ...state, tableData:  state.tableData.concat(action.payload) };
    case VIEW_DATA:
      return { state };
    case DELETE_DATA:
      return { ...state, tableData: [] };
    default:
      return state;
  }
}
