import React, { useState} from 'react';
import {AppTable} from '../../../../../../../common/components';
import styles from '../../../rates.module.css';

function TaxList(props) {
    const {rentals,cols,deleteFun,editTax,taxList}=props;

    return (
        <>
            {rentals && rentals.map((rnt,idx)=>(
                <div key={idx} >
                <div className={styles.taxHeader}>
                    {rnt}
                </div>
                <AppTable 
                    cols={cols} 
                    tableName="Taxes" 
                    isEdit="true"
                    onEdit={editTax}
                    isDelete="true"
                    rows={taxList.filter((tax)=>tax.rentalSelection===rnt)}
                    onDelete={(row)=>deleteFun(row.id)}
                />
                </div>
            ))}
        </>
    )
}
export default TaxList;