import {
    ADD_NEW_RATE,
    GET_RATE_LIST,
    ADD_NEW_FEE,
    DELETE_FEE,
    EDIT_FEE_LIST,
    EDIT_RATES,
    ADD_NEW_TAX,
    DELETE_TAX,
    EDIT_TAX_LIST,
  } from './types';
import {BACKEND_API} from "../../config/middleware";
import API from "../../config/api-url";

export const addRate = data => ({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:ADD_NEW_RATE,
            method: 'post',
            url: API.RATE,
            data
        },
    )
});

export const rateList=()=>({
    type: BACKEND_API,
        payload: Object.assign(
        {
            type:GET_RATE_LIST,
            method: 'get',
            url: API.RATE,
        },
    )
})

export const editRates=(data)=>({
    type: BACKEND_API,
    payload: Object.assign(
        {
            type:EDIT_RATES,
            method: 'put',
            url: API.RATE,
            data
        },
    )
})

export const addFee = data => ({
    type: ADD_NEW_FEE,
    payload: data
});

export const deleteFee=id=>({
    type: DELETE_FEE,
    payload: id
})

export const editFees=data=>({
    type: EDIT_FEE_LIST,
    payload: data
})

export const addTax = data => ({
    type: ADD_NEW_TAX,
    payload: data
});

export const deleteTax=id=>({
    type: DELETE_TAX,
    payload: id
})
export const editTax=data=>({
    type: EDIT_TAX_LIST,
    payload: data
})