import React from 'react';
import { Nav, NavDropdown, Navbar } from 'react-bootstrap';
import { NavLink, Link } from 'react-router-dom';

import styles from './topbar.module.css';
import logo from '../../../../icons/logo.png';
import address from '../../../../icons/address-book-regular.svg';
import help from '../../../../icons/question-circle.svg';
import person from '../../../../icons/person-lines.svg';
import calendar from '../../../../icons/calendar-alt-regular.svg';
import settingIcon from '../../../../icons/settings.svg';
import rentalB from '../../../../icons/rental_building.png';
import hotel from '../../../../icons/hotel-solid.svg';
import invoice from '../../../../icons/file-invoice-dollar-solid.svg';

import {useSelector, useDispatch} from 'react-redux';
import { logoutUser } from '../../../../general_redux/user/actions';

function TopBar() {
  const links = [
    { lable: 'Rentals', img: rentalB, to: '/view/rentals', icon: 'fa-building ' },
    { lable: 'Rates', img: address, to: '/view/rates' },
    { lable: 'Settings', img: settingIcon, to: '/view/settings', icon: 'fa fa-cogs' },
  ];
  const sideLinks = [
      {lable: 'Calendar', img: calendar, to: '/view/calendar'},
      {lable: 'Guests', img: address, to: '/view/guest'},
  ];

  const user = useSelector(({user}) => user && user.user);
  const dispatch = useDispatch();
  const logOut = () => {
    dispatch(logoutUser());
  }
  return (
    <>
      <Navbar collapseOnSelect expand="lg" variant="light" className={styles.topBarPanel}>
        <Navbar.Brand href="/view">
          <div className="d-none d-xl-block  d-lg-block">
             <img src={logo} className={styles.logoHeader} />
          </div>
          <div className={`d-flex d-xl-none d-xs-none  d-lg-none justify-content-between ${styles.header_sm_menu}`} >
             <img src={logo} className={styles.logoHeader} />
             <div className="d-flex">
             {sideLinks && sideLinks.map((item) => (
            <Nav.Item className={`d-flex justify-content-center pt-1`}>
                <NavLink  to={item.to}  activeClassName={styles.isActive} className={`w-100 d-flex flex-column align-items-center`}>
                    <img
                        src={item.img}
                        className={styles.sidebar_img}
                        width="34px"
                        height="24px"
                    />
                </NavLink >
            </Nav.Item>
        ))}</div>
          </div>
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="responsive-navbar-nav " />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto d-flex justify-content-around">
            {links &&
              links.map((item, i) => (
                <NavLink
                  to={item.to}
                  className={`${styles.topLink} ml-5`}
                  key={`topMenu_${i}`}
                  activeClassName={styles.isActive}
                >
                  <div className={styles.linkcontainer}>
                    {item.img ? <img src={item.img} className={styles.imgStyle} /> : 
                      <i className={`fa ${item.icon} fa-2x mt-2`}></i>
                     }
                    <span className={styles.topLinkText}>{item.lable}</span>
                  </div>
                </NavLink>
              ))}
          </Nav>
          <Nav>
            <img src={help} className={styles.imgStyle} />
            <img src={person} className={styles.imgStyle} />
            <div className={styles.dropdownBox}>
              <NavDropdown
                title={user.name}
                id="collapsible-nav-dropdown"
                className={styles.dropdownStyle}
              >
                <NavDropdown.Item id="settings">
                  <Link to="/view/userSetting">User Settings</Link>
                </NavDropdown.Item>
                <NavDropdown.Item id="logout" onClick={logOut}>
                  Logout
                </NavDropdown.Item>
              </NavDropdown>
            </div>
            <div className={styles.hiddenLink}>
              <div><Link to="/view/userSetting">User Settings</Link></div>
              <div>
                <Link onClick={logOut}>
                  Logout
                </Link>
              </div>
            </div>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </>
  );
}
export default TopBar;
