import React from 'react';
import {Button,Modal, Tabs, Tab} from 'react-bootstrap';
import styles from '../../../Rentals/rentals.module.css';
import {FormInput} from '../../../../../../common/components';
import {handleSubmit} from '../../../../../../common/functions/get-form-value'; 
import GuestInvoiceList from '../List/GuestInvoiceList';
import GuestBookingList from '../List/GuestBookingList';

function AddGuests(props){
  const fields = [
    {label: 'Name', type: 'text', placeholder: 'Full Name', value: 'guest_name',field: 'guest_name',
      defaultVal: (props.viewForEdit)?props.viewForEdit.guest_name :null },
    {label: 'Company', type: 'text', placeholder: '', value: 'company',field: 'company',
      defaultVal: (props.viewForEdit)?props.viewForEdit.company :null },
    {label: 'E-mail', type: 'mail', placeholder: 'example@example.com', field: 'email',value: 'email',
      defaultVal: (props.viewForEdit)?props.viewForEdit.email :null },
    {label: 'Secondary E-mail', type: 'mail', placeholder: 'example2@example.com', value: 'email_secondary',field: 'email_secondary',
      defaultVal: (props.viewForEdit)?props.viewForEdit.email_secondary :null},
    {label: 'Phone', type: 'number', placeholder: 'Phone', value: 'phone',field: 'phone',
      defaultVal: (props.viewForEdit)?props.viewForEdit.phone :null},
    {label: 'Country', type: 'text', placeholder: 'Country', value: '',field: 'country',
      defaultVal: (props.viewForEdit)?props.viewForEdit.country :null},
    {label: 'Street and NR', type: 'text', placeholder: '', value: 'street',field: 'street',
      defaultVal: (props.viewForEdit)?props.viewForEdit.street :null},
    {label: 'Postal Code', type: 'number', placeholder: '', value: 'postal',field: 'postal',
      defaultVal: (props.viewForEdit)?props.viewForEdit.postal :null},
    {label: 'state/Provinance', type: 'text', placeholder: '', value: 'state',field: 'state',
      defaultVal: (props.viewForEdit)?props.viewForEdit.state :null},
    {label: 'Nationality', type: 'text', placeholder: '', value: 'nationality',field: 'nationality',
      defaultVal: (props.viewForEdit)?props.viewForEdit.nationality :null},
    {label: 'Language', type: 'text', placeholder: '', value: 'language', field: 'language',
      defaultVal: (props.viewForEdit)?props.viewForEdit.language :null},
    {label: 'Notes', type: 'text', placeholder: '', value: 'notes',field: 'notes',
      defaultVal: (props.viewForEdit)?props.viewForEdit.notes :null},
  ]
  
  const addData = () => {
    handleSubmit('add-guest', fields, (data) => {
      const payload={
        name:data.guest_name,
        emailId:data.email,
        phoneNo:data.phone,
        customerId:2,
        secondaryEmailId:data.email_secondary,
        country:data.country,
        address:data.street,
        postalCode:data.postal,
        state:data.state,
        nationality:data.nationality,
        language:data.language,
        notes:data.notes
      }
      if(props.viewForEdit){
        props.onEdit(payload)
      }else{
        props.onEdit(payload)
      }
    })
  }
    return(
        <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
            Add Guest Details
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className={styles.deleteModalBody}>
        {props.viewForEdit ? 
          <Tabs defaultActiveKey="details" id="uncontrolled-tab-example">
          <Tab eventKey="details" title="Guest Details">
          <form id="add-guest" >
            {fields && fields.map((d) => <FormInput {...d} value={props && props.value && d.field && props.value[d.field] || ''}/>)}
          </form>
          </Tab>
          <Tab eventKey="booking" title="Guest Booking">
            <GuestBookingList/>
          </Tab>
          <Tab eventKey="invoice" title="Guest Invoice" >
            <GuestInvoiceList/>
          </Tab>
        </Tabs> : 
          <form id="add-guest" >
            {fields && fields.map((d) => <FormInput {...d} value={props && props.value && d.field && props.value[d.field] || ''}/>)}
          </form>
        }
        
      </Modal.Body>
      <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
          <Button variant="primary" type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'} onClick={addData}>Save changes</Button>
      </div>
    </Modal>
    )
}
export default AddGuests;