import React from 'react';
import {Button,Form,InputGroup} from 'react-bootstrap';
import { Formik } from 'formik';

function GroupForm(props){
    const {schema,handleSubmit,options} = props;
    return(
        <>
            <Formik
                validationSchema={schema}
                initialValues={(props.initValue)? props.initValue : {
                    groupName: '',
                    color: '',
                }}
                onSubmit={handleSubmit}
                validateOnChange={false}
                validateOnBlur={false}
            >
                {({
                      errors,handleChange,handleSubmit,touched,initialValues
                  }) => (
                    <Form onSubmit={handleSubmit}>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Group Name</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="text"
                                    name="groupName"
                                    onChange={handleChange}
                                    isInvalid={!!errors.groupName }
                                    defaultValue={initialValues.groupName}
                                />
                                <Form.Control.Feedback type="invalid">{errors.groupName}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Color</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    as="select"
                                    name="color"
                                    onChange={handleChange}
                                    isInvalid={!!errors.color}
                                >
                                    <option key={'selectC'} selected={true} disabled={true}>----Select----</option>
                                    {options.map((opt)=>(<option key={opt} selected={initialValues.color===opt}>{opt}</option>))}
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">{errors.color}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
                            <Button variant="primary" disabled={false} type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>Save changes</Button>
                        </div>
                    </Form>
                )}
            </Formik>
        </>
    )
}

export default GroupForm;