import React, { useState} from 'react';
import {AppTable} from '../../../../../../common/components';
import {useSelector, useDispatch} from 'react-redux';
import {Container, Row, Col} from 'react-bootstrap';
import _ from "lodash";
import { deleteGuest } from '../../../../../../general_redux/guest/actions';


function GuestBookingList(props) {
    let guestList = useSelector(({guests}) => guests && guests.guestList);
    const dispatch = useDispatch();
    const cols = [
        {lable: 'check In', key: 'checkIn'},
        {lable: 'check Out', key: 'checkOut'},
        {lable: 'Nights', key: 'nights'},
        {lable: 'Rental', key: 'rental'},
        {lable: 'Channel', key: 'channel'},
        {lable: 'Total', key: 'total'},
        {lable: 'Status', key: 'status'},
        {lable: 'Actions', key: ''},
    ]
   
    const handleDelete=(row)=>{
        dispatch(deleteGuest(row.id));
    }
    if (props.search) {
        const search = props.search.toLowerCase();
        guestList = _.filter(guestList, (o) => Object.values(o).map(value => value && value.toLowerCase() || '').filter(value => value.includes(search)).length > 0);
    }

    return (
        <div>
            <Container fluid>
                <AppTable 
                cols={cols}  
                tableName="Guests"
                rows={guestList}
                isView
                isOnlyEdit
            />
            </Container>
        </div>
    )
}
export default GuestBookingList;