import React, { useEffect,useState } from 'react';
import { Link } from 'react-router-dom';
import {useSelector,useDispatch} from "react-redux";
import styles from './register.module.css';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import logo from '../../icons/logo.png';
import {registerUser} from '../../general_redux/user/actions';

function Register(props) {
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [company, setCompanyName] = useState('');
  const [website, setWebsite] = useState('');
  const [propertyType, setPropertyType] = useState('');
  const [numberOfUnits, setNumberOfUnits] = useState('');
  const registerStatus = useSelector(({ user }) => user && user.registerAction);
  const dispatch=useDispatch();

  useEffect(()=>{
    if(registerStatus.success){
      //props.history.push(`/select_plan`);
    }
  },[registerStatus])

  function handleSubmit(e) {
    e.preventDefault();
    const data = {
      name: `${firstName} ${lastName}`,
      emailId: email,
      companyName: company,
      website: website,
      propertyType: "rented",
      noOfUnits: numberOfUnits,
    };
    dispatch(registerUser(data));
  }

  return (
    <div>
      <Header />
      <div className="d-flex justify-content-center">
        <div className={`${styles.mainContainer} ${styles.register}`}>
          <div className={`${styles.register_wrapper} shadow`}>
            <div className="d-flex justify-content-center">
              <Link to="/">
                <img src={logo} className={styles.register_image} />
              </Link>
            </div>
            <div
              className={`d-flex justify-content-center ${styles.register_header}`}
            >
              <h5>Account Set Up</h5>
            </div>
            <div className="d-flex justify-content-center">
              <form onSubmit={handleSubmit}>
                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <span
                      className={`input-group-text ${styles.input_group_text}`}
                      id="firstName"
                    >
                      First Name
                    </span>
                  </div>
                  <input
                    onChange={(e) => setFirstName(e.target.value)}
                    type="text"
                    className={`form-control ${styles.form_control}`}
                    aria-label="firstName"
                    aria-describedby="firstName"
                  ></input>
                </div>

                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <span
                      className={`input-group-text ${styles.input_group_text}`}
                      id="lastName"
                    >
                      Last Name
                    </span>
                  </div>
                  <input
                    onChange={(e) => setLastName(e.target.value)}
                    type="text"
                    className={`form-control ${styles.form_control}`}
                    aria-label="lastName"
                    aria-describedby="lastName"
                  ></input>
                </div>

                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <span
                      className={`input-group-text ${styles.input_group_text}`}
                      id="emailAddress"
                    >
                      Email Address
                    </span>
                  </div>
                  <input
                    onChange={(e) => setEmail(e.target.value)}
                    type="text"
                    className={`form-control ${styles.form_control}`}
                    aria-label="emailAddress"
                    aria-describedby="emailAddress"
                  ></input>
                </div>

                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <span
                      className={`input-group-text ${styles.input_group_text}`}
                      id="companyName"
                    >
                      Company Name
                    </span>
                  </div>
                  <input
                    onChange={(e) => setCompanyName(e.target.value)}
                    type="text"
                    className={`form-control ${styles.form_control}`}
                    aria-label="companyName"
                    aria-describedby="companyName"
                  ></input>
                </div>

                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <span
                      className={`input-group-text ${styles.input_group_text}`}
                      id="website"
                    >
                      Website
                    </span>
                  </div>
                  <input
                    onChange={(e) => setWebsite(e.target.value)}
                    type="text"
                    className={`form-control ${styles.form_control}`}
                    aria-label="website"
                    aria-describedby="website"
                  ></input>
                </div>

                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <label
                      className={`input-group-text ${styles.input_group_text}`}
                      for="propertyType"
                    >
                      Property Type
                    </label>
                  </div>
                  <select
                    onChange={(e) => setPropertyType(e.target.value)}
                    className={`custom-select ${styles.form_control}`}
                    id="propertyType"
                  >
                    <option selected>Please select...</option>
                    <option value="Apartment">Apartment</option>
                    <option value="Townhomes">Townhomes</option>
                    <option value="Condominium">Condominium</option>
                    <option value="Room">Room</option>
                  </select>
                </div>

                <div className={`input-group mb-3 ${styles.input_group}`}>
                  <div className="input-group-prepend">
                    <span
                      className={`input-group-text ${styles.input_group_text}`}
                      id="numberOfUnits"
                    >
                      Number Of Units
                    </span>
                  </div>
                  <input
                    onChange={(e) => setNumberOfUnits(e.target.value)}
                    type="text"
                    className={`form-control ${styles.form_control}`}
                    aria-label="numberOfUnits"
                    aria-describedby="numberOfUnits"
                  ></input>
                </div>
                <div className="d-flex justify-content-center">
                  <button className={styles.register_submit} >
                    Submit
                  </button>
                </div>
              </form>
            </div>
            <div className="d-flex justify-content-center">
              <Link to="/select_plan" className={styles.back_to_home}>
                Cancel
              </Link>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Register;
