import React,{useState} from 'react';
import styles from '../../rates.module.css';
import {useSelector,useDispatch} from 'react-redux';
import AddTax from './AddTax/AddTax';
import TaxList from './List/TaxList';
import {addTax,deleteTax,editTax} from '../../../../../../general_redux/rates/actions';

function Taxes(props){
    const [addTaxModal,setAddTaxModal] =useState(false);
    const [edited,setEdited]= useState(false);
    const dispatch=useDispatch();

    const rentalList=useSelector(({rentals}) => rentals && rentals.rentals);
    const rentals=rentalList.map((rnt)=>rnt.name);
    const taxList = useSelector(({rates}) => rates && rates.taxList);
    
    const cols= [
        {lable: 'Name', key: 'tax_name'},
        {lable: 'Type', key: 'tax_type'},
        {lable: 'Amount', key: 'amount'},
        {lable: 'Modality', key: 'modality'},
        {lable:'Actions' , key:''},
    ];
    
    const closeModal = (data) => {
        if(edited){
            dispatch(editTax(data));
        }else{
            dispatch(addTax(data));
        }
        setEdited(null);
        setAddTaxModal(false);
    }
    const editRow=(val)=>{
        setAddTaxModal(true)
        setEdited(val);
    }
    const deleteRow=(id)=>{
        dispatch(deleteTax(id));
    }
    return(
        <>
            <AddTax
                show={addTaxModal}
                onHide={() => {
                    setAddTaxModal(false);
                    setEdited(null); }}
                onEdit={(data) => closeModal(data)}
                viewforedit={edited}
            />
            <div className={styles.rateSettingHeader}>
                Tax Settings
            </div>
            <button className={styles.allBtn} onClick={()=>setAddTaxModal(true)}>Add Tax</button>
            
            <TaxList
                rentals={rentals}
                cols={cols}
                deleteFun={(id)=>deleteRow(id)}
                taxList={taxList}
                editTax={editRow}
            />
        </>
    )
}

export default Taxes;