import React,{useState} from 'react';
import { ExportToCsv } from 'export-to-csv';
import styles from './guest.module.css';
import {useSelector,useDispatch} from 'react-redux';
import SearchField from "react-search-field";
import ListGuests from './components/List/ListGuests';
import AddGuests from './components/AddGuests/AddGuests';
import {addGuests,editGuest} from '../../../../general_redux/guest/actions';

function Guests(props) {
    const dispatch = useDispatch();
    const [addGuestModal,setAddGuestModal]=useState(false);
    const [edited,setEdited] = useState(null);
    const [search, setSearch] = useState('');
    let guestList = useSelector(({guests}) => guests && guests.guestList);

    const closeModal = (data) => {
        if(edited){
            dispatch(editGuest(data));
        }else{
            dispatch(addGuests(data));
        }
        setAddGuestModal(false);
        setEdited(null);
    }
    const editGroupData = (value) => {
        setEdited(value);
        setAddGuestModal(true);
    }
    const handleSearch=(val)=>{
        setSearch(val);
    }
    const handleExport=()=>{
        const options = {
            fieldSeparator: ',',
            quoteStrings: '"',
            decimalSeparator: '.',
            showLabels: true,
            showTitle: true,
            title: `Guest List `,
            useTextFile: false,
            filename:`Beehaz_guest_list_${Date.now()}`,
            useBom: true,
            useKeysAsHeaders: true,
        };
        const csvExporter = new ExportToCsv(options);
        let data = [];
        const csvData = guestList.filter((guest) => guest.name.toLocaleLowerCase().includes(search));
        if (csvData.length !== 0) {
            csvData.forEach((row) => {
                data.push({
                    Name: row.guest_name,
                    Company: row.company,
                    Email: row.email,
                    Phone: row.phone
                })
            });
            csvExporter.generateCsv(data);
        }
    }
    return(
        <>
        <AddGuests
            show={addGuestModal}
            onHide={() => {
                setAddGuestModal(false);
                setEdited(null); }}
            onEdit={(data) => closeModal(data)}
            viewForEdit={edited}
        />
         <div className={styles.guestHeader}>
            <div className={styles.searchContainer}>
                <SearchField
                    placeholder="Search Guests"
                    onChange={(val)=>handleSearch(val)}
                    searchText=""
                    classNames="test-class"
                />
            </div>
            <div className={styles.flexEnd}>
                <button className={`${styles.allBtn} w-25 ${styles.viewBtn}` } onClick={handleExport}>Download Excel</button>
                <button className={`${styles.allBtn} w-25`} onClick={()=>setAddGuestModal(true)}>Add Guest</button>
            </div>
        </div>
        <ListGuests editGuests={editGroupData} search={search} data={guestList.filter((guest)=>guest.name.toLocaleLowerCase().includes(search))}/>
        </>
    )
}

export default Guests;