import React,{useState} from 'react';
import {useSelector} from "react-redux";
import {Modal} from 'react-bootstrap';
import * as yup from 'yup';
import RentalForm from "./RentalForm";

import styles from '../../rentals.module.css';

function AddRental(props){
    const schemaRental = yup.object({
        name: yup.string().required(),
        addressLine1: yup.string(),
        addressLine2: yup.string(),
        postalCode: yup.string().required(),
        country: yup.string().required(),
        currency:yup.string(),
        groupId:yup.number().required(),
        maxGuests:yup.number().required(),
        checkInTime:yup.string(),
        checkOutTime:yup.string(),
    });
    const groups = useSelector(({rentals}) => rentals && rentals.groups);
    const countries = useSelector(({general}) => general && general.countries);
    const options_currency= useSelector(({general}) => general && general.currency);

    const handleSubmit=(data)=>{
        const payload={
            id:(props.value)?props.value.id :undefined,
            name: data.name,
            postalCode: data.postalCode,
            addressLine1: data.addressLine1,
            addressLine2: data.addressLine2,
            country: data.country,
            groupId: data.groupId,
            maxGuests: data.maxGuests,
            currency: data.currency,
            checkInTime:data.checkInTime,
            checkOutTime:data.checkOutTime,
        }
        if(props.value){
            props.onEdit(payload);
        }else{
            props.onHide(payload);
        }
    }
    return(
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Add/Edit Rental
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className={styles.deleteModalBody}>
                <RentalForm
                    schema={schemaRental}
                    options_currency={options_currency}
                    options_country={countries}
                    handleSubmit={handleSubmit}
                    options_groups= {groups}
                    initValue={(props.value) ? props.value: false}
                />
            </Modal.Body>
        </Modal>
    )
}
export default AddRental;