import React from 'react';
import {Button,Form,InputGroup} from 'react-bootstrap';
import { Formik } from 'formik';

function RentalForm(props){
    const {schema,handleSubmit,options_country,options_currency,options_groups} = props;
    return(
        <>
            <Formik
                validationSchema={schema}
                initialValues={(props.initValue)? props.initValue : {
                    name: '',
                    addressLine1: '',
                    addressLine2: '',
                    postalCode: '',
                    country: '',
                    currency:'',
                    groupId:'',
                    maxGuests:'',
                }}
                onSubmit={handleSubmit}
                validateOnChange={false}
                validateOnBlur={false}
            >
                {({
                      errors,handleChange,handleSubmit,touched,initialValues
                  }) => (
                    <Form onSubmit={handleSubmit}>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Rental Name</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="text"
                                    name="name"
                                    onChange={handleChange}
                                    isInvalid={!!errors.name }
                                    defaultValue={initialValues.name}
                                />
                                <Form.Control.Feedback type="invalid">{errors.name}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Street and no</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="text"
                                    name="addressLine1"
                                    onChange={handleChange}
                                    isInvalid={!!errors.addressLine1 }
                                    defaultValue={initialValues.addressLine1}
                                />
                                <Form.Control.Feedback type="invalid">{errors.addressLine1}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Landmark</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="text"
                                    name="addressLine2"
                                    onChange={handleChange}
                                    isInvalid={!!errors.addressLine2 }
                                    defaultValue={initialValues.addressLine2}
                                />
                                <Form.Control.Feedback type="invalid">{errors.addressLine2}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Postal Code</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="text"
                                    name="postalCode"
                                    onChange={handleChange}
                                    isInvalid={!!errors.postalCode }
                                    defaultValue={initialValues.postalCode}
                                />
                                <Form.Control.Feedback type="invalid">{errors.postalCode}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Country</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    as="select"
                                    name="country"
                                    onChange={handleChange}
                                    isInvalid={!!errors.country}
                                >
                                    <option key={'selectC'} selected={true} disabled={true}>----Select----</option>
                                    {options_country.map((opt)=>(<option key={opt} selected={initialValues.country===opt}>{opt}</option>))}
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">{errors.country}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Currency</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    as="select"
                                    name="currency"
                                    onChange={handleChange}
                                    isInvalid={!!errors.currency}
                                >
                                    <option key={'selectCurrency'} selected={true} disabled={true}>----Select----</option>
                                    {options_currency.map((opt)=>(<option key={opt} selected={initialValues.currency===opt}>{opt}</option>))}
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">{errors.currency}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Select Group</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    as="select"
                                    name="groupId"
                                    onChange={handleChange}
                                    isInvalid={!!errors.groupId}
                                >
                                <option key={'selectG'} selected={true} disabled={true}>----Select----</option>
                                {options_groups.map((opt)=>(<option key={opt.id} value={opt.id} selected={initialValues.groupId===opt.id}>{opt.groupName}</option>))}
                                </Form.Control>
                                <Form.Control.Feedback type="invalid">{errors.groupId}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Check In Time</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="time"
                                    name="checkInTime"
                                    onChange={handleChange}
                                    isInvalid={!!errors.checkInTime }
                                    defaultValue={initialValues.checkInTime}
                                />
                                <Form.Control.Feedback type="invalid">{errors.checkInTime}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Check Out Time</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="time"
                                    name="checkOutTime"
                                    onChange={handleChange}
                                    isInvalid={!!errors.checkOutTime }
                                    defaultValue={initialValues.checkOutTime}
                                />
                                <Form.Control.Feedback type="invalid">{errors.checkOutTime}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <Form.Row>
                            <Form.Label className="col-sm-4 col-form-label">Maximum guests</Form.Label>
                            <div className="col-md-4">
                                <Form.Control
                                    type="number"
                                    name="maxGuests"
                                    onChange={handleChange}
                                    isInvalid={!!errors.maxGuests }
                                    defaultValue={initialValues.maxGuests}
                                />
                                <Form.Control.Feedback type="invalid">{errors.maxGuests}</Form.Control.Feedback>
                            </div>
                        </Form.Row>
                        <br/>
                        <div className={`{styles.modalFooter} p-2 d-flex justify-content-center `}>
                            <Button variant="primary" disabled={false} type="submit" className={'col-lg-4 p-2 mr-4 col-sm-12'}>Save changes</Button>
                        </div>
                    </Form>
                )}
            </Formik>
        </>
    )
}

export default RentalForm;